
import iHeader from "@/components/header/header-r";
import { TabContainer, TabContainerItem,LoadMore,Popup,Toast } from "mint-ui";
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: "orderall",
data() {
return {
users: localStorage.getItem('users') ? JSON.parse(localStorage.getItem('users')) : '',
active: 1, // 2 3 4
popupVisible:false,
reloadState:1,
loading:false,
arrow:true,
hash:[{id:1,name:"全部"},{id:2,name:"待确认"},{id:3,name:"已确认"},{id:4,name:"退款中"}],
state:0,
list:[],
classstate:'',
page:1,
popupVisible:false,
more_state:1,
};
},
components: {
iHeader,
TabContainer,
TabContainerItem,
LoadMore,
Popup,
XHeader
},
methods: {
tabBtn(type) {
this.active = type;
if(type==1){
this.state=-999;
this.list=[];
this.getlist();
}else if(type==2){
this.state=-2;
this.list=[];
this.getlist();
}else if(type==3){
this.state=1;
this.list=[];
this.getlist();
}else if(type==4){
this.state=2;
this.list=[];
this.getlist();
}

},
getlist(page = 1,callback,reload){
this.$axios.post('/api/Loan_post/loan_post_order',`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
page:page,
status:this.state,
})}`).then((results)=>{
if(results.code==200){
callback&&callback();
if(reload){

this.list = results.data.list;
this.reloadState = 1;
return false;
}
if(results.data.list.length<=results.data.pageSize){
this.more_state = 2;
this.loading = true;
this.allLoaded=true;
}
this.$nextTick(function () {
this.list = [...this.list,...results.data.list];
});

}else if(results.code==401){
this.reloadState = 2;
this.$router.push('/my/login');
};
}).catch((err)=>{this.reloadState = 3;});
},
loadMore(){


this.page++;
this.loading = true;
this.getlist(this.page,()=>{
this.loading = false;
});
},
loadTop(){
this.page = 1;
this.getlist(this.page,()=>{
this.popupVisible = true;
this.$refs.loadmore.onTopLoaded();
this.more_state = 1;
this.loadMore();
setTimeout(()=>{
this.popupVisible = false;
},2000);
},true)
},

},

mounted(){
var indexactive=this.$route.query.type;
this.tabBtn(indexactive);


}
};



